(function (diskupon) {
    diskupon.GlobalEvents = {
        tickRelayoutMasonry: function () {
            if (typeof window.msnry !== 'undefined') {
                window.msnry.layout();
                var times = 0;
                var msnryTimeout = window.setInterval(function () {
                    //console.log('masonry relayout');
                    window.msnry.layout();
                    times++;
                    if (times > 30) {
                        window.clearInterval(msnryTimeout);
                    }
                }, 300);
            }
        }
    };
} (window.Diskupon));